html,
body {
  overflow-x: clip;
}

@media (min-width: 776px) {
  .masked-background {
    background-image: linear-gradient(
      to bottom,
      rgba(255, 255, 255, 1) 60%,
      rgba(255, 255, 255, 0) 100%
    );
    background-repeat: no-repeat;
    background-size: 100% 100%;
  }

  .masked-bottom {
    background-image: linear-gradient(
      to top,
      rgba(255, 255, 255, 1) 60%,
      rgba(255, 255, 255, 0) 100%
    );
    background-repeat: no-repeat;
    background-size: 100% 100%;
  }
}
@media (max-width: 776px) {
  .sticky-images {
    background: linear-gradient(180deg, #fff 84.97%, rgba(255, 255, 255, 0) 100%) !important;
  }
}
.gsap-marker-start,
.gsap-marker-end,
.gsap-marker-scroller-start,
.gsap-marker-scroller-end {
  opacity: 1 !important;
  display: block !important;
  z-index: 99999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999999 !important;
}

